import { isText } from '@whisklabs/typeguards';
import some from 'lodash/some';
import { whiskyUrl } from 'constants/config';
var exceptions = ['chrome://', 'https://chrome.google.com', whiskyUrl];
export var isInvalidUrl = function (url) {
    return !isText(url) || some(exceptions, function (x) { return url.startsWith(x); });
};
